}
blockquote:before, blockquote:after, q:before, q:after {
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

b, strong {
  font-weight: bold;
}

i, em {
  font-style: italic;
}

sup {
  vertical-align: super;
  font-size: smaller;
}

sub {
  vertical-align: sub;
  font-size: smaller;
}

*,
*:before,
*:after {
  outline: none;
  box-sizing: border-box;
}

input,
textarea,
select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

